import { useEffect, useState } from 'react';
import { tempoParaSegundos } from '../../common/util/time';
import { CronometroWrapper } from '../../styles/Cronometro';
import { InterfaceTarefa } from '../../types/interfaceTarefa';
import { Botao } from '../Button';
import Relogio   from './Relogio';

interface Props {
    selecionado: InterfaceTarefa | undefined,
    finalizarTarefa: () => void
    /* O primeiro no esta selecionado, ento  preciso dizer que o selecionado tambm pode ser undefined */
}

export function Cronometro({ selecionado, finalizarTarefa } : Props ) {
    const [tempo, setTempo] = useState<number>();
    useEffect(() => {
        if(selecionado?.tempo){
            setTempo(tempoParaSegundos(selecionado.tempo))    /* if(selecionado?.tempo) -> Se selecionado existir -> se selecionado.tempo existir -> executar if */ 
        }
    }, [selecionado])     /* Sempre que selecionado mudar, executar a funo */
    
    function regressiva(contador: number = 0){
        setTimeout(() => {
            if(contador > 0){
                setTempo(contador - 1)
                return regressiva(contador -1) /* LOOP - Funo recursiva    */
            }
            finalizarTarefa()
        }, 1000)
    }
    
    return (
        <CronometroWrapper>
            <p className='titulo'>Escolha um card e inicie o cronmetro</p>
            <div className='relogioWrapper'>
                <Relogio tempo={tempo}/>
                <Botao onClick={() => regressiva(tempo)}>Comear!</Botao>
            </div>
        </CronometroWrapper>
    )
}